<script>
import { ref } from 'vue';
export default {
    setup() {
        const active = ref(0);
        const list = ref([]);
        const loading = ref(false);
        const finished = ref(false);
        const onLoad = () => {
            // 异步更新数据
            // setTimeout 仅做示例，真实场景中一般为 ajax 请求
            setTimeout(() => {
                for (let i = 0; i < 10; i++) {
                    list.value.push(list.value.length + 1);
                }

                // 加载状态结束
                loading.value = false;

                // 数据全部加载完成
                if (list.value.length >= 40) {
                    finished.value = true;
                }
            }, 1000);
        };
        return {
            active,
            list,
            onLoad,
            loading,
            finished,
        };
    },
}
</script>

